<template>
    <div>
        <div class="processes-item inblock" :style="{width: width}" v-for="(item, index) in dataLists" :key="index">
            <p class="pad-l processes-item-con">能耗： {{item.eneCon.value}}{{item.eneCon.unit}}</p>
            <div>
                <img src="../../../assets/images/right-arrow.png">
                <span class="processes-item-name">{{item.name}}</span>
            </div>
            <div class="text-center">
                <img src="../../../assets/images/bottom-arrow.png">
            </div>
            <div class="pad-l processes-panel-wrap" :style="{height: productHeight}">
                <p v-for="(item2, index2) in item.productLists" :key="index2">{{item2.name}}:{{item2.value}}{{item2.unit}}</p>
            </div>
            <div class="pad-l processes-panel-wrap">
                <p>产值： {{item.energy.output}}{{item.energy.outputUnit}}</p>
                <p>单耗： {{item.energy.consump}}{{item.energy.consumpUnit}}</p>
                <p>定额： {{item.energy.quota}}{{item.energy.quotaUnit}}</p>
                <p>标杆： {{item.energy.benchmark}}{{item.energy.benchmarkUnit}}</p>
            </div>
            <div class="pad-l">
                <p v-for="(item3, index3) in item.eneType" :key="index3">{{item3.name}}:{{item3.value}}{{item3.unit}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: "240px"
    },
    dataLists: {
      type: Array,
      default() {
        return [
          {
            name: "蛋白质车间",
            eneCon: {
              value: "222",
              unit: "tce"
            },
            productLists: [
              {
                name: "精粉",
                value: 165.21,
                unit: "吨"
              },
              {
                name: "通粉",
                value: 55.32,
                unit: "吨"
              }
            ],
            energy: {
              output: "123",
              outputUnit: "吨",

              consump: "234",
              consumpUnit: "吨",

              quota: "453",
              quotaUnit: "吨",

              benchmark: "635",
              benchmarkUnit: "吨"
            },
            eneType: [
              {
                name: "电耗",
                value: "123",
                unit: "Kw"
              },
              {
                name: "蒸汽",
                value: "123",
                unit: "t"
              }
            ]
          }
        ];
      }
    }
  },
  data() {
    return {};
  },
  computed: {
    //  计算产品的高度
    productHeight() {
      let maxIndex = 0;
      this.$each(this.dataLists, item => {
        if (item.productLists.length > maxIndex) {
          maxIndex = item.productLists.length;
        }
      });
      return maxIndex * 26 + 20 + "px";
    }
  }
};
</script>

<style scoped>
.processes-item .pad-l {
  padding-left: 54px;
}
.processes-item {
  vertical-align: top;
  font-size: 16px;
  font-weight: 400;
}

.processes-item > * {
  padding: 5px 0;
}

.processes-item-name {
  padding: 30px 40px;
  background-color: #69c;
  color: #fff;
  line-height: 80px;
}

.processes-item img {
  vertical-align: middle;
}

.processes-panel-wrap {
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}
</style>



