<template>
  <div>
    <da-panel :no-head="true" padding-content="ltbr">
      <label for="">生产部门--{{department}}</label>
      <da-radio-select v-model="department" :maxNum="1" :option-lists="departmentLists" label="name" value-key="id"></da-radio-select>
      <label for="">时间--{{time}}--{{timeType}}</label>
      <da-time-select v-model="time" :time-type.sync="timeType" :show-time="[3,5,8]"></da-time-select>
      <el-button @click="search">查询</el-button>
    </da-panel>

    <processes-item :data-lists="lists"></processes-item>
  </div>
</template>
<script>
import processesItem from "../template/processesItem";
import { cycleTime, extend } from "../../../util/util";
export default {
  name: "workingProcedureTargetAnalysis",
  components: {
    processesItem
  },
  data() {
    return {
      time: "",
      timeType: 3,
      department: "",
      departmentLists: [], // 生产部门
      processesLists: [], // 工序

      lists: [
        {
          name: "蛋白质车间",
          eneCon: {
            value: "222",
            unit: "tce"
          },
          productLists: [
            {
              name: "精粉",
              value: 165.21,
              unit: "吨"
            },
            {
              name: "通粉",
              value: 55.32,
              unit: "吨"
            }
          ],
          energy: {
            output: "123",
            outputUnit: "吨",

            consump: "234",
            consumpUnit: "吨",

            quota: "453",
            quotaUnit: "吨",

            benchmark: "635",
            benchmarkUnit: "吨"
          },
          eneType: [
            {
              name: "电耗",
              value: "123",
              unit: "Kw"
            },
            {
              name: "蒸汽",
              value: "123",
              unit: "t"
            }
          ]
        },
        {
          name: "酒精车间",
          eneCon: {
            value: "222",
            unit: "tce"
          },
          productLists: [
            {
              name: "精粉",
              value: 165.21,
              unit: "吨"
            }
          ],
          energy: {
            output: "123",
            outputUnit: "吨",

            consump: "234",
            consumpUnit: "吨",

            quota: "453",
            quotaUnit: "吨",

            benchmark: "635",
            benchmarkUnit: "吨"
          },
          eneType: [
            {
              name: "电耗",
              value: "123",
              unit: "Kw"
            },
            {
              name: "蒸汽",
              value: "123",
              unit: "t"
            }
          ]
        }
      ]
    };
  },
  methods: {
    // 获取
    getDepart() {
      this.$ajax
        .get("rest/analysisData/parentOrg", {
          params: {
            title: "查询部门列表",
            orgTypes: "3",
            parentIds: ""
          }
        })
        .then(res => {
          if (res.data.status) {
            this.departmentLists = res.data.records;
          }
        });
    },
    // 获取工序
    getProcesses() {
      return this.$ajax
        .get("rest/analysisData/depart", {
          params: {
            title: "根据部门查询工序",
            orgTypes: "3",
            parentIds: this.department
          }
        })
        .then(res => {
          this.processesLists = res.data.records;
          this.getEnergy().then(res => {
            console.log(res.data);
          });
        });
    },
    // 获取产值、定额、标杆
    getTarget() {
      return this.$ajax.get("");
    },
    getApi(query) {
      let timeRange = cycleTime(this.time, this.timeType);
      let orgIds = this.processesLists
        .map(item => {
          return item.id;
        })
        .join(",");
      extend(query, {
        orgIds: orgIds,
        targetCycle: this.timeType, // String,指标周期
        startTime: timeRange[0],
        endTime: timeRange[1]
      });
      return this.$ajax.get("rest/analysisData/targetData", {
        params: query
      });
    },
    // 获取单耗、能耗、#产量
    getEnergy() {
      let timeRange = cycleTime(this.time, this.timeType);
      let targetType = this.$store.state.targetConstant.targetType;
      let query = {
        title: "获取能源消耗", // String,功能标题
        targetTypes: targetType.energyConsum,
        //  +
        // "," +
        // targetType.harvest +
        // "," +
        // targetType.unitProductEnergyConsum, // String,指标类型
        // contentTypes: this.$store.state.targetConstant.targetContentType.energy, // String,统计内容类型
        // contentIds: this.$store.state.targetConstant.energy.compreEnergyConsum, //this.rdsEnergySubitem, // String,统计内容ID
        objTypes: null, // String,指标关联对象类型
        objTypeValues: null, // String,指标关联对象值
        isQueryAll: false, // Boolean,是否所有指标数据
        isOrgId: true, // Boolean,是否返回部门ID
        isContentType: false, // Boolean,是否返回指标统计内容类型ID
        isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品};
      };
      return this.getApi(query);
    },
    search() {
      this.getProcesses();
    }
  },
  mounted() {
    this.getDepart();
  }
};
</script>




